import React, { HTMLAttributes, ReactElement } from 'react';
import cs from 'classnames';
import { ReactComponent as LogoSVG } from '../../../images/logo.svg';
import style from './style.module.scss';

interface LogoProps {
    className?: string
}

export const Logo = ({ className, ...props }: LogoProps & HTMLAttributes<HTMLOrSVGElement>): ReactElement => <LogoSVG className={cs(style.logo, className)} {...props} />;

Logo.defaultProps = {
  className: undefined,
};
