import React, { Dispatch, SetStateAction } from 'react';

export interface ContextProps { token: string | null, refreshToken: string | null, setToken: Dispatch<SetStateAction<string | null>>, setRefreshToken: Dispatch<SetStateAction<string | null>>, signIn: (data?: any) => Promise<{ token: string, refreshToken: string } | null>, refresh: (data?: any) => Promise<string | null>, isLoggedIn: boolean, setLoggedIn: Dispatch<SetStateAction<boolean>> }

export const authContext = React.createContext<ContextProps>({
  token: null,
  refreshToken: null,
  isLoggedIn: false,
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  setToken: () => {},
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  setRefreshToken: () => {
  },
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  setLoggedIn: () => {
  },
  signIn: async () => null,
  refresh: async () => null,
});
export const AuthProvider = authContext.Provider;
